<template>
    <div class="descripcion-content">
        <div class="container-fluid panel-filtros">
            <div class="container pb-4">                        
                <div class="row justify-content-md-center">
                    <div class="col-6 col-sm-3 col-md-3 col-lg-2 pl-1 pr-1 pt-4"> 
                        <multiselect  :multiple="false" :close-on-select="true" :clear-on-select="false" :preserve-search="true" :searchable="false" :options="tipoDoc" 
                                v-model="seleccionadosE" deselect-label="Quitar" select-label="Seleccionar" track-by="name" label="name">
                                <template slot="singleLabel" slot-scope="{ option }">
                                    <strong>{{option.name}}</strong>
                                </template>
                        </multiselect>
                    </div>
                    <div class="col-6 col-sm-3 col-md-3 col-lg-2 pl-1 pr-1 pt-4"> 
                        <input class="form-control" v-model="numDocText" placeholder="Número de documento" maxlength="15">
                    </div>
                    <div class="col-6 col-sm-3 col-md-3 col-lg-2 pl-1 pr-1 pt-4">
                        <input class="form-control text-center" v-model="aPapeletaText" placeholder="Año de papeleta" @keypress="isNumber($event)" maxlength="4">                               
                    </div>
                    <div class="col-6 col-sm-3 col-md-3 col-lg-2 pl-1 pr-1 pt-4">
                        <input class="form-control text-center" v-model="nPapeletaText" placeholder="Número de papeleta" @keypress="isNumber($event)" maxlength="10">                                
                    </div>
                    <div class="col d-md-none">&nbsp;</div>
                    <div class="col-12 col-sm-12 col-md-12 col-lg-3 pt-4 text-center">
                        <button class="btn btn-info" @click.prevent="seleccionarMulta()">Buscar</button>
                        <button class="btn btn-warning" @click.prevent="limpiarCampoFiltro">
                            <b-icon-trash></b-icon-trash>
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import EventBus from '../../helpers/EventBus'
import Multiselect from 'vue-multiselect'

export default {
    components:{ Multiselect },
    data(){
        return {
            selected: null,
            tdi: '',
            con: '',
            estadoCuenta: {},
            tributos: [],
            tributosVisible: [],
            dataContribuyente: {},
            predios: [],
            prediosSeleccionados: [],
            tipoDocSeleccionado: '',
            totalInsoluto: 0,
            monto01: 0,
            venPag: '',
            recibosSeleccionados: [],
            recibosSeleccionadosTemp: [],
            recibosSeleccionadosTodos: false,
            boolNuevaBusqueda:false,
            datosCargados: false,
            saldoRelacionadoInquilino: false,
            opcSelTodos: false,
            opcPagarTodo: [],
            hayDatos: true,
            tieneContribuyentesAsociados: false,
            emisionActual: 0,
            estadoSeleccionado: 0,
            estadoInicial:'ANC',
            estadosSel: '',
            periodoI:'',
            periodoF:'',
            boolGrill:false,
            aPapeletaText:'',
            nPapeletaText:'',
            numDocText: '',
            fecD:0,
            fecH:0,
            boolFiltroPeriodo:false,
            boolFiltroAño:false,
            seleccionadosE: [{ name: 'DNI', letra: 'D' }],
            seleccionadosP: [],
            tipoDoc: [
                { name: 'DNI', letra: 'D' },
                { name: 'RUC', letra: 'R' },
                { name: 'OTRO', letra: 'O' },
            ]
        }
    }
}
</script>
<style lang="scss">
    @import '../../assets/css/tributos.scss';
</style>


